  /********sub banner******/
  
  .sub_banner {
    width: 100%;
    margin-bottom: 100px;
  
    /* padding: 0 20px; */
    /* margin-top: 70px; */
  
    .banner_wrap {
      height: 380px;
      position: relative;
  
      img {
        width: 100%;
        height: 100%;
      }
  
      .background {
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
      }
  
      .span_ex {
        position: absolute;
        bottom: 40px;
        left: 25px;
      }
  
      .background span {
        color: #fff;
        font-size: 30px;
        margin-right: 10px;
      }
    }
  }
  
  /***********optsection*********************/
  
  .opt_section {
    width: 100%;
    padding: 0 20px;
  }
  
  .opt_wrap {
    display: flex;
  
    .opt_box1 {
      width: 20%;
    }
  }
  
  #filter_chk {
    display: none;
  
    &:checked ~ {
      .filter_open {
        display: none;
      }
  
      .filter_close {
        display: block;
      }
    }
  
    ~ {
      .filter_open {
        display: block;
      }
  
      .filter_close {
        display: none;
      }
    }
  }
  
  .opt_wrap {
    .opt_box2 {
      width: 60%;
    }
  
    .opt_box3 {
      width: 20%;
      display: flex;
  
      .sort_box {
        width: 50%;
  
        select {
          width: 100%;
          height: 25px;
          outline: none;
          border: 0;
        }
      }
  
      .grid_box {
        width: 50%;
        display: flex;
        justify-content: flex-end;
        font-size: 25px;
  
        i {
          margin-left: 20px;
          vertical-align: top;
        }
      }
  
      .grid_01, .grid_02 {
        cursor: pointer;
      }
    }
  }
  
  /*그리드 너비*/
  
  .sublist_wrap {
    &.gridchange01 .subBox {
      width: calc(100% - 80% - 24px);
    }
  
    &.gridchange02 .subBox {
      width: calc(100% - 75% - 25px);
    }
