    .grid_01 {
      color: black;
    }
  
    .grid_02 {
      color: #ccc;
    }
  
    .grid_01::after {
      content: "";
      display: inline-block;
      vertical-align: top;
      width: 1px;
      height: 25px;
      margin-left: 20px;
      background-color: #ccc;
    }
