  /**********상품리스트*************/
  
  .sublist {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
  
  .subBox {
    width: calc(100% - 75% - 25px);
  
    /* height: 550px; */
    margin-left: 20px;
    margin-bottom: 50px;
    cursor: pointer;
    transition: .3s;
    display: block;
  }
  
  .subimg_wrap {
    font-size: 0;
    position: relative;
    overflow: hidden;
    transition: .3s;
  
    &:hover {
      img {
        transform: scale(1.05);
      }
  
      box-shadow: 0 0 15px black;
  
      .hoverBox {
        /* opacity: 1; */
        bottom: 0;
      }
    }
  }
  
  .hoverBox {
    width: 100%;
    height: 50px;
    line-height: 50px;
    position: absolute;
    z-index: 999;
    bottom: -50px;
  
    /* opacity: 0; */
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: .3s;
  }
  
  .hoverWrap {
    display: flex;
    text-align: center;
    color: #fff;
  }
  
  .like {
    width: 50%;
    font-size: 25px;
    transition: .3s;
    cursor: pointer;
  
    &:hover {
      color: red;
    }
  }
  
  .cart {
    width: 50%;
    font-size: 25px;
    transition: .3s;
    cursor: pointer;
  
    &:hover {
      color: #002053;
    }
  }
  
  .subimg {
    width: 100%;
    transition: .5s;
  }
  
  .subBox:nth-child(4n) {
    margin-right: 0;
  }
  
