    /* display: flex; */
  }
  
  .item_desc_wrap {
    width: 100%;
  }
  
  .item_category {
    font-size: 12px;
    margin: 10px 0;
  }
  
  .item_name {
    font-size: 17px;
    margin-bottom: 10px;
  }
  
  .item_price {}
  
