    width: 100%;
    text-align: center;
    margin-top: 10px;
  
    /* position: relative; */
  }
  
  .buy_btn {
    &:hover {
      background-color: #002053;
      color: #fff;
      border: #002053;
      box-shadow: 0 0 10px black;
    }
  
    width: 100%;
    height: 40px;
    line-height: 40px;
    border-radius: 30px;
    border: 1px solid #ccc;
    background-color: #eee;
  
    /* position: absolute; */
    /* bottom: 0; */
    /* left: 50%; */
    /* transform: translateX(-50%); */
    transition: .3s;
  }
  
  /*********footer********/
  
  footer {
    width: 100%;
    text-align: center;
    margin-top: 100px;
  }
  
  .footer_wrap {
    border-top: 1px solid #ccc;
    background-color: #eee;
    padding: 20px 10px;
  
    .footer_box1 {
      display: flex;
  
      .foot_logo {
        width: 20%;
  
        img {
          width: 90px;
          height: 30px;
        }
      }
  
      .foot_menu01 {
        width: 20%;
      }
  
      h4 {
        font-size: 20px;
        margin-bottom: 20px;
        cursor: pointer;
      }
  
      li {
        margin-bottom: 10px;
        cursor: pointer;
      }
  
      .foot_menu02, .foot_menu03, .foot_menu04 {
        width: 20%;
      }
    }
  
    .footer_box2 {
      margin-top: 50px;
  
      .foot_menu05 {
        display: flex;
  
        li {
          margin-bottom: 30px;
          cursor: pointer;
          font-weight: bold;
  
